import { Vue, Component, Prop, Model, Watch } from "vue-property-decorator";
import api from "@/microApps/sensorManage/api";
import moment from "moment";
import echarts from "echarts";

@Component({
  filters: {
    timeFilter: function(b: string) {
      if (b) {
        return moment(b).format("YYYY-MM-DD HH:mm:ss");
      }
    }
  }
})
export default class SensorDataStatistics extends Vue {
  @Prop({ type: String, required: true })
  id!: string;

  @Watch("id", { immediate: true })
  handleIdChange(val: string) {
    if (val) {
      this.getCurrentData();
    } else {
      clearTimeout(this.timer);
    }
  }

  dateType: string = "";
  presentData: any[] = [];
  data: any[] = []; // 
  lineChart: any;
  timer: any;
  timeQuantum: string[] = [];

  // 
  get chartData() {
    return this.data.map((d: any) => {
      if (d.chartData) {
        return {
          dataList: d.chartData.dataList.map((a: any) => {
            return {
              ...a,
              sensorName: d.sensorName
            };
          }),
          xaxisData: d.chartData.xaxisData,
          sensorName: d.sensorName
        };
      } else {
        return {
          dataList: [],
          xaxisData: [],
          sensorName: ""
        };
      }
    });
  }

  // 
  @Watch("chartData")
  handleChange() {
    this.renderChart(this.chartData);
  }

  // 
  get currentTableData() {
    let result: any[] = [];
    if (this.presentData.length) {
      result = this.presentData.map((d: any) => d.currentData).reduce((a: any[], b: any[]) => a.concat(b));
    }
    return result;
  }

  mounted() {
    this.lineChart = echarts.init(this.$refs.chart as any);
  }

  destroyed() {
    this.lineChart.dispose();
    clearTimeout(this.timer);
  }

  async getHistoryData() {
    if (!this.dateType || this.dateType === "") {
      this.$message.error("");
      return;
    }
    this.timerStatus = "queryHistoryData";
    if (this.lineChart) this.lineChart.clear();
    this.timeQuantum = [];
    try {
      let {
        data: { status, data, message }
      } = await this.$http.get(`${api.sensorHistory}/${this.id}/${this.dateType}`);
      if (status) {
        this.data = data;
        // 
        this.timerStatus = "queryCurrentData";
      } else {
        this.$message.error(message);
      }
    } finally {
      // clearTimeout(this.timer);
    }
  }
  async getCurrentData() {
    try {
      let {
        data: { status, data, message }
      } = await this.$http.get(`${api.sensorCurrentData}/${this.id}`);
      if (status) {
        if (data) {
          this.presentData = data;
          if (this.timerStatus === "queryCurrentData") {
            this.mergeCurrentData(data);
          }
        }
      } else {
        this.$message.error(message);
      }
    } finally {
      this.timerScheduler();
    }
  }

  // 
  mergeCurrentData(data: any[]) {
    let presentDate: number = new Date().valueOf();
    let dateTypeNum: number = 2;
    switch (this.dateType) {
      case "quarter":
        dateTypeNum = 0.25;
        break;
      case "half":
        dateTypeNum = 0.5;
        break;
      case "threeQuarters":
        dateTypeNum = 0.75;
        break;
      case "hour":
        dateTypeNum = 1;
        break;
      case "halfAndHour":
        dateTypeNum = 1.5;
        break;
      default:
        dateTypeNum = 2;
    }
    //   
    let historySensorIdDataMap: any = {};
    this.data.forEach((item: any) => {
      historySensorIdDataMap[item.sensorId] = item;
    });
    if (
      data.some((item: any) => {
        return !historySensorIdDataMap[item.sensorId] && presentDate - new Date(item.currentData[0].time).valueOf() < dateTypeNum * 3600000;
      })
    ) {
      this.getHistoryData();
      return;
    }

    let sensorIdDataMap: any = {}; // ID mapID
    data.forEach((item: any) => {
      sensorIdDataMap[item.sensorId] = item;
    });
    this.data.forEach((item: any) => {
      //   
      let historyAttrCodeDataMap: any = {};
      item.chartData.dataList.forEach((attr: any) => {
        historyAttrCodeDataMap[attr.code] = attr;
      });
      let sensor = sensorIdDataMap[item.sensorId];
      if (
        sensor.currentData.some((attr: any) => {
          return !historyAttrCodeDataMap[attr.code];
        })
      ) {
        this.getHistoryData();
        return;
      }
      let attrCodeDataMap: any = {}; // code mapcode
      sensor.currentData.forEach((attr: any) => {
        attrCodeDataMap[attr.code] = attr;
      });
      let currentDataTime = sensor.currentData[0].time;
      if (item.chartData && item.chartData.xaxisData[item.chartData.xaxisData.length - 1] !== currentDataTime) {
        item.chartData.dataList.forEach((attr: any) => {
          let currentAttr = attrCodeDataMap[attr.code];
          if (currentAttr === undefined) {
            attr.data.push("");
          } else {
            attr.data.push(currentAttr.value);
          }
        });

        item.chartData.xaxisData.push(currentDataTime);
        item.currentData = sensor.currentData;
      }
      if (presentDate - new Date(item.chartData.xaxisData[0]).valueOf() > dateTypeNum * 3600000) {
        item.chartData.xaxisData.shift();
        item.chartData.dataList.forEach((m: any) => {
          m.data.shift();
        });
      }
    });
  }

  // 
  async getTimeQuantumData() {
    if (!this.timeQuantum.length || this.timeQuantum === []) {
      this.$message.error("");
      return;
    }
    this.timerStatus = "queryTimeQuantumData";
    this.dateType = "";
    if (this.lineChart) this.lineChart.clear();
    try {
      let {
        data: { status, data, message }
      } = await this.$http.post(`${api.getTimeQuantumData}/${this.id}`, {
        startTime: this.timeQuantum[0],
        endTime: this.timeQuantum[1]
      });
      if (status) {
        this.data = data;
      } else {
        this.$message.error(message);
      }
    } catch (e) {
      this.$message.error("");
    }
  }

  // 
  timerStatus: "init" | "queryHistoryData" | "queryCurrentData" | "queryTimeQuantumData" = "init";

  // 
  timerScheduler() {
    if ((this as any)._isDestroyed) return;
    this.timer = setTimeout(() => {
      if ((this as any)._isDestroyed) return;
      switch (this.timerStatus) {
        case "init":
        case "queryTimeQuantumData":
        case "queryCurrentData":
          this.getCurrentData();
          break;
        case "queryHistoryData":
          this.getHistoryData();
          this.getCurrentData();
          break;
      }
    }, 10000);
  }

  renderSensorNum: number = 0;

  renderChart(data: any) {
    if (this.renderSensorNum && data.length !== this.renderSensorNum) {
      this.lineChart.clear(); //   
    }
    this.renderSensorNum = data.length;
    if (!data.length) return;
    this.lineChart.setOption({
      tooltip: {
        trigger: "axis",
        formatter: function(p: any) {
          let str = p
            .map((i: any) => {
              if (i.data.value) return `${i.marker} ${i.seriesName}: ${i.data.value} ${i.data.unit ? i.data.unit : ""}<br/>`;
              return "";
            })
            .join("");
          return `${p[0].axisValue}<br/>${str}`;
        }
      },
      legend: {
        data: data[0].dataList.map((d: any) => `${d.name}`)
      },
      grid: {
        left: 50,
        right: 50,
        top: 50,
        bottom: 50,
        containLabel: false
      },
      xAxis: {
        type: "category",
        boundaryGap: false,
        data: data[0].xaxisData.map((d: string) => moment(d).format("HH:mm:ss"))
      },
      yAxis: data[0].dataList.map(() => {
        return {
          type: "value",
          show: false
        };
      }),
      series: data[0].dataList.map((d: any, index: number) => {
        return {
          yAxisIndex: index,
          name: d.name,
          type: "line",
          data: d.data.map((item: any) => {
            return {
              value: item,
              unit: d.unit
            };
          })
        };
      })
    });
  }
}
