










































































































































































import { Vue, Component, Prop, Model, Watch } from "vue-property-decorator";
import utils from "@/utils/utils";
import api from "@/microApps/sensorManage/api";
import SensorDetail from "./components/SensorDetail.vue";
import SensorDataStatistics from "./components/SensorDataStatistics.vue";
import cookieUtils from "@/utils/cookie";

@Component({
  components: {
    SensorDetail,
    SensorDataStatistics
  }
})
export default class SensorList extends Vue {
  loading: boolean = false;
  sensorName: string = "";
  searchDTO = {
    total: 0,
    pageNum: 1,
    pageSize: 10,
    sensorTypeId: "",
    name: ""
  };
  typeOptions: Array<any> = [];
  typeOptionsProps: any = {
    value: "id",
    label: "name",
    showAllLevels: false,
    checkStrictly: true
  };
  sensorTypeDetails = [];
  sensorData: Array<any> = [];
  //
  sensorDataFormVisible: boolean = false;
  //
  dataForm: object = {};
  //
  sensorForm: object = {};
  //
  sensorDialogTitle: string = "";
  //visible
  sensorDialogVisible: boolean = false;
  // 
  sensorDataVisible: boolean = false;
  sensorDataId: string = "";
  uploadProcessFileUrl: string = api.upload;
  httpHeaders: any = {
    accessToken: "Bearer " + cookieUtils.get("accessToken")
  };
  uploadFileList: any[] = [];
  getTableData() {}

  //options
  getSensorTypeData() {
    this.$http.get(api.sensorTypeTree).then((res: any) => {
      if (res.data.status) {
        this.typeOptions = res.data.data;
        utils.removeEmptyChildren(this.typeOptions);
      } else {
        this.$message.error(res.data.message);
      }
    });
  }

  showDataForm(data: any) {
    this.sensorDataFormVisible = true;
    this.$http.get(api.getSensorData + "/" + data.id).then((res: any) => {
      if (res.data.status) {
        if (res.data.data === null) {
          this.dataForm = {};
          this.$message.info("");
        } else {
          this.dataForm = res.data.data;
          this.sensorTypeDetails = res.data.data.values;
        }
      } else {
        this.$message.error(res.data.message);
      }
    });
  }

  showDataChart(data: any) {
    this.sensorDataVisible = true;
    this.sensorDataId = data.id;
  }

  closeDialog() {
    this.dataForm = {};
    this.sensorForm = {};
    this.sensorDataFormVisible = false;
    this.sensorDialogVisible = false;
  }

  handleEdit(data: any) {
    this.sensorDialogTitle = "";
    this.dataForm = JSON.parse(JSON.stringify(data));
    this.sensorDialogVisible = true;
  }
  handleDelete(id: number) {
    let _this = this;
    this.$confirm("", "", {
      confirmButtonText: "",
      cancelButtonText: "",
      type: "warning"
    })
      .then(() => {
        this.$http.get(api.deleteSensorList + "/" + id).then((res: any) => {
          if (res.status) {
            if (res.data.status) {
              this.$message({
                type: "success",
                message: "!"
              });
              //
              _this.getSensorList();
            } else {
              _this.$message({
                showClose: true,
                message: res.data.message,
                type: "error"
              });
            }
          }
        });
      })
      .catch(() => {});
  }

  handleAdd() {
    this.sensorDialogTitle = "";
    this.dataForm = {};
    this.sensorDialogVisible = true;
  }

  pageCurrentChange(current: number) {
    this.searchDTO.pageNum = current;
    this.getSensorList();
  }

  pageSizeChange(size: number) {
    this.searchDTO.pageSize = size;
    this.getSensorList();
  }

  getSearchList() {
    this.searchDTO.pageNum = 1;
    this.getSensorList();
  }
  //
  getSensorList() {
    this.loading = true;
    let searchInfo: any = JSON.parse(
      JSON.stringify({
        sensorTypeId: this.searchDTO.sensorTypeId,
        name: this.searchDTO.name
      })
    );
    searchInfo.sensorTypeId = utils.lastOfArr(searchInfo.sensorTypeId);
    this.$http
      .post(api.getSensorList, {
        query: searchInfo,
        current: this.searchDTO.pageNum,
        size: this.searchDTO.pageSize
      })
      .then((res: any) => {
        this.loading = false;
        if (res.data.status) {
          this.sensorData = res.data.data.records;
          this.searchDTO.total = res.data.data.total;
        } else {
          this.$message.error(res.data.message);
        }
      })
      .catch(() => {
        this.loading = false;
      });
  }

  downloadExcel() {
    let a = document.createElement("a");
    a.href = api.download;
    a.click();
  }

  uploadFileSuccess(data: any, file: any) {
    const chosenFile: any = file;
    if (data.status) {
      this.$message.success(data.message);
      this.getSensorList();
    } else {
      this.$message.error(data.message);
      this.getSensorList();
    }
  }

  uploadFileError() {
    this.$message.error("");
  }

  mounted() {
    this.getSensorList();
    this.getSensorTypeData();
  }
}
